exports.push([module.id, "\n.my-custom-dialog[data-v-a493cdac] {\r\n  -ms-flex-item-align: end;\r\n      align-self: flex-end;\n}\r\n", "", {"version":3,"sources":["C:/test/썬퍼센트/sunpercent/src/components/src/components/ContentDetail.vue"],"names":[],"mappings":";AAwmBA;EACA,yBAAA;MAAA,qBAAA;CACA","file":"ContentDetail.vue","sourcesContent":["<template>\r\n  <div class=\"contentdetail pa-6 center\">\r\n    {{pre_url_set}}\r\n    <v-col cols=\"12\">\r\n      <v-row justify=\"center\">\r\n        <v-card width=\"100%\" max-width=\"780px\" class=\"justify-center\">\r\n          <!--\r\n            v-img 음영효과 넣기 옵션\r\n            gradient=\"to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)\"-->\r\n          <v-img\r\n            v-if=\"this.$store.state.imagestore.imagedetail[0].report_count > 2\"\r\n            :src=\"black_image\"\r\n            class=\"grey--text align-center\"\r\n            aspect-ratio=\"1\"\r\n            ><v-card-text class=\"font-weight-thin\" align=\"center\"\r\n              >신고누적으로<br />이미지차단</v-card-text\r\n            ></v-img\r\n          >\r\n          <v-img\r\n            v-else\r\n            :src=\"\r\n              `http://192.168.0.12:4000${this.$store.state.imagestore.imagedetail[0].image_path}`\r\n            \"\r\n            class=\"white--text align-end mx-auto\"\r\n            gradient=\"to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)\"\r\n            max-height=\"500px\"\r\n            max-width=\"1000px\"\r\n            @click=\"newWindowImage\"\r\n            contain\r\n          >\r\n            <template v-slot:placeholder>\r\n              <v-row class=\"fill-height ma-0\" align=\"center\" justify=\"center\">\r\n                <v-progress-circular\r\n                  indeterminate\r\n                  color=\"grey lighten-5\"\r\n                ></v-progress-circular>\r\n              </v-row>\r\n            </template>\r\n          </v-img>\r\n          <template>\r\n            <div>\r\n              <v-row class=\"mt-6 ml-3\">\r\n                <v-icon\r\n                  class=\"mr-1\"\r\n                  color=\"blue darken-3\"\r\n                  v-if=\"\r\n                    this.$store.state.imagestore.imagedetail[0].gender == `male`\r\n                  \"\r\n                  >mdi-alpha-m-circle-outline</v-icon\r\n                >\r\n                <v-icon\r\n                  class=\"mr-1\"\r\n                  color=\"pink\"\r\n                  v-if=\"\r\n                    this.$store.state.imagestore.imagedetail[0].gender ==\r\n                      `female`\r\n                  \"\r\n                  >mdi-alpha-w-circle-outline</v-icon\r\n                ><span class=\"ml-1\">{{\r\n                  this.$store.state.imagestore.imagedetail[0].nickname\r\n                }}</span>\r\n                <v-icon class=\"ml-6\">mdi-eye-outline</v-icon>\r\n                <span class=\"ml-2\">\r\n                  {{ this.$store.state.imagestore.imagedetail[0].view_count }}\r\n                </span>\r\n                <v-icon class=\"ml-6\">mdi-bullhorn-outline</v-icon>\r\n                <span class=\"ml-2\">{{\r\n                  this.$store.state.imagestore.imagedetail[0].report_count\r\n                }}</span>\r\n              </v-row>\r\n              <v-card-text\r\n                v-text=\"this.$store.state.imagestore.imagedetail[0].date\"\r\n              ></v-card-text>\r\n            </div>\r\n          </template>\r\n\r\n          <v-alert\r\n            v-if=\"\r\n              this.$store.state.scorestore.scorestate.filter(\r\n                item =>\r\n                  item.from_uid ==\r\n                  this.$store.state.loginstore.userstate[0].user_uid\r\n              ).length > 0\r\n            \"\r\n            type=\"success\"\r\n            >내가 점수 등록한 게시물</v-alert\r\n          >\r\n          <v-card-actions>\r\n            <v-spacer></v-spacer>\r\n            <v-btn\r\n              v-if=\"\r\n                this.$store.state.scorestore.scorestate.filter(\r\n                  item =>\r\n                    item.from_uid ==\r\n                    this.$store.state.loginstore.userstate[0].user_uid\r\n                ).length <= 0 &&\r\n                  this.$store.state.imagestore.imagedetail[0].user_uid !=\r\n                    this.$store.state.loginstore.userstate[0].user_uid\r\n              \"\r\n              text\r\n              color=\"primary\"\r\n              dark\r\n              @click.prevent=\"score\"\r\n              >점수주기</v-btn\r\n            >\r\n            <v-btn\r\n              text\r\n              color=\"primary\"\r\n              v-if=\"\r\n                this.$store.state.imagestore.imagedetail[0].user_uid ==\r\n                  this.$store.state.loginstore.userstate[0].user_uid\r\n              \"\r\n              dark\r\n              @click.prevent=\"delete_dialog\"\r\n              >삭제하기</v-btn\r\n            >\r\n\r\n            <v-btn\r\n              v-if=\"\r\n                this.$store.state.imagestore.imagedetail[0].user_uid !=\r\n                  this.$store.state.loginstore.userstate[0].user_uid\r\n              \"\r\n              text\r\n              color=\"primary\"\r\n              :to=\"\r\n                `/userpage/${$store.state.imagestore.imagedetail[0].nickname}/${$store.state.imagestore.imagedetail[0].user_uid}`\r\n              \"\r\n              >프로필보기</v-btn\r\n            >\r\n            <v-btn\r\n              text\r\n              color=\"primary\"\r\n              v-if=\"\r\n                this.$store.state.imagestore.imagedetail[0].user_uid !=\r\n                  this.$store.state.loginstore.userstate[0].user_uid\r\n              \"\r\n              :to=\"\r\n                `/note/${$store.state.imagestore.imagedetail[0].nickname}/${$store.state.imagestore.imagedetail[0].user_uid}`\r\n              \"\r\n              >쪽지보내기</v-btn\r\n            >\r\n            <v-btn\r\n              v-if=\"\r\n                this.$store.state.imagestore.imagedetail[0].user_uid !=\r\n                  this.$store.state.loginstore.userstate[0].user_uid\r\n              \"\r\n              text\r\n              color=\"grey\"\r\n              @click=\"report\"\r\n              >신고하기</v-btn\r\n            >\r\n          </v-card-actions>\r\n        </v-card>\r\n      </v-row>\r\n    </v-col>\r\n    <!-- 별점주기 -->\r\n\r\n    <div class=\"text-center\">\r\n      <v-dialog v-model=\"scoredialog\" width=\"400\" persistent>\r\n        <v-card class=\"elevation-16 mx-auto \" width=\"400\">\r\n          <v-card-title class=\"text-h5\">\r\n            이 사진의 외모는 몇점 입니까?\r\n          </v-card-title>\r\n          <v-card-text>\r\n            해당 게시물 점수는 게시물 작성자에게만 표시 됩니다.<br />(7점 만점)\r\n            <div class=\"text-center mt-12\">\r\n              <v-rating\r\n                v-model=\"rating\"\r\n                color=\"yellow darken-3\"\r\n                background-color=\"grey darken-1\"\r\n                empty-icon=\"$ratingFull\"\r\n                half-increments\r\n                length=\"7\"\r\n                x-large\r\n              ></v-rating>\r\n              <v-card-text>{{ this.rating }} / 7</v-card-text>\r\n            </div>\r\n          </v-card-text>\r\n          <v-divider></v-divider>\r\n          <v-card-actions class=\"justify-space-between\">\r\n            <v-btn text @click=\"score_cancel\">\r\n              취소\r\n            </v-btn>\r\n            <v-btn color=\"primary\" text @click.prevent=\"score_send\">\r\n              점수 보내기\r\n            </v-btn>\r\n          </v-card-actions>\r\n        </v-card>\r\n      </v-dialog>\r\n    </div>\r\n    <div class=\"text-center\">\r\n      <v-dialog v-model=\"deletedialog\" width=\"400\" persistent>\r\n        <v-card class=\"elevation-16 mx-auto \" width=\"400\">\r\n          <v-card-title class=\"text-h10 justify-center\">\r\n            사진과 점수를 삭제합니다.\r\n          </v-card-title>\r\n          <v-card-text class=\"text-center mt-5\">\r\n            해당 게시물을 삭제하시겠습니까?\r\n          </v-card-text>\r\n          <v-divider></v-divider>\r\n          <v-card-actions class=\"justify-space-between\">\r\n            <v-btn text @click=\"delete_cancel\">\r\n              취소\r\n            </v-btn>\r\n            <v-btn color=\"primary\" text @click.prevent=\"deleteimage\">\r\n              삭제\r\n            </v-btn>\r\n          </v-card-actions>\r\n        </v-card>\r\n      </v-dialog>\r\n    </div>\r\n    <div class=\"text-center\" ref=\"report_form\" align=\"center\" justify=\"center\">\r\n      <v-dialog v-model=\"reportdialog\" width=\"400\" persistent>\r\n        <v-card class=\"elevation-16 mx-auto \" width=\"400\">\r\n          <v-card-title class=\"text-h10 justify-center\">\r\n            신고하기\r\n          </v-card-title>\r\n          <v-divider></v-divider>\r\n          <v-card-text class=\"text-center mt-5\">\r\n            해당 게시물을 신고하시겠습니까?\r\n          </v-card-text>\r\n          <v-form ref=\"report_form\">\r\n            <v-row align=\"center\" justify=\"center\">\r\n              <v-col class=\"d-flex\" cols=\"12\" sm=\"10\">\r\n                <v-select\r\n                  :items=\"report_reason_list\"\r\n                  label=\"신고사유를 선택해 주세요.\"\r\n                  outlined\r\n                  required\r\n                  :rules=\"reportRules\"\r\n                  v-model=\"report_reason\"\r\n                ></v-select>\r\n              </v-col>\r\n            </v-row>\r\n          </v-form>\r\n          <v-card-actions class=\"justify-space-between\">\r\n            <v-btn text @click=\"report_cancel\">\r\n              취소\r\n            </v-btn>\r\n            <v-btn color=\"primary\" text @click.prevent=\"report_content\">\r\n              신고\r\n            </v-btn>\r\n          </v-card-actions>\r\n        </v-card>\r\n      </v-dialog>\r\n    </div>\r\n    <div>\r\n      <v-dialog v-model=\"showimage_dialog\" width=\"auto\"\r\n        ><v-img\r\n          max-width=\"100%\"\r\n          max-height=\"100%\"\r\n          :src=\"\r\n            `http://192.168.0.12:4000${this.$store.state.imagestore.imagedetail[0].image_path}`\r\n          \"\r\n          ><v-col class=\"text-right\"\r\n            ><v-icon large right @click=\"showimage_dialog_close\" dark\r\n              >mdi-close</v-icon\r\n            ></v-col\r\n          ></v-img\r\n        ></v-dialog\r\n      >\r\n    </div>\r\n    <div\r\n      class=\"mt-10 mr-7\"\r\n      v-if=\"\r\n        this.$store.state.imagestore.imagedetail[0].user_uid ===\r\n          this.$store.state.loginstore.userstate[0].user_uid\r\n      \"\r\n    >\r\n      <apexcharts\r\n        class=\"d-flex justify-center\"\r\n        height=\"350\"\r\n        type=\"bar\"\r\n        :options=\"chartOptions\"\r\n        :series=\"series\"\r\n      />\r\n      <div>\r\n        <v-row justify=\"center\">\r\n          총합계 평균점수 : {{ average_total }}&nbsp;({{ total_number }}명)\r\n          <v-col cols=\"1\"></v-col> 남자 : {{ average_male }}&nbsp;({{\r\n            male_number\r\n          }}명) &nbsp;&nbsp;&nbsp; 여자 : {{ average_female }}&nbsp;({{\r\n            female_number\r\n          }}명)\r\n        </v-row>\r\n      </div>\r\n    </div>\r\n    <div v-else class=\"pt-10\" align=\"center\">\r\n      <v-alert\r\n        align=\"start\"\r\n        border=\"left\"\r\n        color=\"indigo\"\r\n        dark\r\n        width=\"100%\"\r\n        max-width=\"780px\"\r\n      >\r\n        총합계 평균점수 및 차트는 해당 게시물의 작성자에게만 표시 됩니다.\r\n      </v-alert>\r\n    </div>\r\n    <v-divider class=\"mt-15 mb-10\"></v-divider>\r\n    <div ><Content></Content></div>\r\n    \r\n  </div>\r\n</template>\r\n<script>\r\nimport VueApexCharts from \"vue-apexcharts\";\r\nimport lodash from \"lodash\";\r\nimport http from \"../http/http\";\r\nimport black_image from \"../assets/black.jpg\";\r\nimport ls from \"localstorage-slim\";\r\nls.config.encrypt = true;\r\nimport Content from './Content.vue'\r\n\r\nexport default {\r\n  components: {\r\n    apexcharts: VueApexCharts,\r\n    Content\r\n  },\r\n  data() {\r\n    return {\r\n      black_image: black_image,\r\n      showimage_dialog: false,\r\n      reportRules: [v => !!v || \"신고사유가 선택되지 않았습니다.\"],\r\n      report_reason: null,\r\n      report_reason_list: [\r\n        \"너무 선정적 입니다.\",\r\n        \"사진을 알아볼 수가 없어요.\",\r\n        \"중복된 게시물 입니다.\",\r\n        \"게시판 성격과 맞지 않아요.\",\r\n        \"광고성 게시물 입니다.\",\r\n        \"사진편집이 과해 보여요\",\r\n        \"외모 사진이 아닙니다.\",\r\n        \"작성자 성별과 사진의 성별이 다릅니다.\"\r\n      ],\r\n      reportdialog: false,\r\n      scoredialog: false,\r\n      deletedialog: false,\r\n      rating: 1,\r\n      chartOptions: {\r\n        theme: {\r\n          mode: \"dark\",\r\n          palette: \"palette1\",\r\n          monochrome: {\r\n            enabled: false,\r\n            color: \"#255aee\",\r\n            shadeTo: \"light\",\r\n            shadeIntensity: 0.65\r\n          }\r\n        },\r\n\r\n        chart: {\r\n          type: \"bar\",\r\n          height: 350,\r\n          background: \"#121212\"\r\n        },\r\n        plotOptions: {\r\n          bar: {\r\n            borderRadius: 4,\r\n            horizontal: true\r\n          }\r\n        },\r\n        dataLabels: {\r\n          enabled: true,\r\n          enabledOnSeries: undefined,\r\n          textAnchor: \"start\",\r\n          distributed: false,\r\n          offsetX: 0,\r\n          offsetY: 0,\r\n          style: {\r\n            fontSize: \"14px\",\r\n            fontFamily: \"Helvetica, Arial, sans-serif\",\r\n            fontWeight: \"bold\",\r\n            colors: undefined\r\n          },\r\n          background: {\r\n            enabled: true,\r\n            foreColor: \"#fff\",\r\n            padding: 4,\r\n            borderRadius: 2,\r\n            borderWidth: 1,\r\n            borderColor: \"#fff\",\r\n            opacity: 0.9,\r\n            dropShadow: {\r\n              enabled: false,\r\n              top: 1,\r\n              left: 1,\r\n              blur: 1,\r\n              color: \"#000\",\r\n              opacity: 0.45\r\n            }\r\n          },\r\n          dropShadow: {\r\n            enabled: false,\r\n            top: 1,\r\n            left: 1,\r\n            blur: 1,\r\n            color: \"#000\",\r\n            opacity: 0.45\r\n          }\r\n        },\r\n        xaxis: { categories: [\"평균점수\"] },\r\n        title: {\r\n          text: \"평균점수(7점만점)\",\r\n          align: \"center\",\r\n          margin: 30,\r\n          offsetX: 0,\r\n          offsetY: 0,\r\n          floating: false,\r\n          style: {\r\n            fontSize: \"25px\",\r\n            fontWeight: \"bold\",\r\n            fontFamily: undefined,\r\n            color: \"#EEEEEE\"\r\n          }\r\n        }\r\n      },\r\n      series: [\r\n        { name: \"전체평균\", data: [] },\r\n        { name: \"남자가 준 평균점수\", data: [] },\r\n        { name: \"여자가 준 평균점수\", data: [] }\r\n      ],\r\n      total_number: 0,\r\n      male_number: 0,\r\n      female_number: 0\r\n    };\r\n  },\r\n  methods: {\r\n    sendNote() {\r\n      this.$router.push(\r\n        `/note/${this.$store.state.imagestore.imagedetail[0].nickname}/${this.$store.state.imagestore.imagedetail[0].user_uid}`\r\n      );\r\n    },\r\n    newWindowImage() {\r\n      window.open(\r\n        `http://192.168.0.12:4000${this.$store.state.imagestore.imagedetail[0].image_path}`,\r\n        \"_blank\"\r\n      );\r\n    },\r\n    report() {\r\n      this.reportdialog = true;\r\n    },\r\n    score() {\r\n      this.scoredialog = true;\r\n    },\r\n    delete_dialog() {\r\n      this.deletedialog = true;\r\n    },\r\n\r\n    score_send() {\r\n      let scoredata = {\r\n        content_uid: this.$store.state.imagestore.imagedetail[0].content_uid,\r\n        to_uid: this.$store.state.imagestore.imagedetail[0].user_uid,\r\n        from_uid: this.$store.state.loginstore.userstate[0].user_uid,\r\n        content_score: this.rating,\r\n        gender: this.$store.state.loginstore.userstate[0].g